import SwiperCore, { Pagination } from 'swiper'
SwiperCore.use([Pagination])

export default {
    components: { 
        VideoBackground ,
        Swiper,
        SwiperSlide
    },
    data () {
        return {
        orders: [
            {
            featureImage: 'images/feature/useful.png',
