        swiperOptions: {
            breakpoints: {
                320: {
                    slidesPerView: 1
                },
                768: {
                    slidesPerView: 2
                },
                1200: {
                    slidesPerView: 3,
                }
            },
            pagination: {
                el: '.swiper-pagination',
                clickable: true,
                type: 'bullets'
            }
        },
        currentPage: 2,
        maxPerPage: 2,
        showReadMore: true
        }
    },
    computed: {
        totalResults () {
        return Object.keys(this.orders).length
        },
        pageCount () {
        return Math.ceil(this.totalResults / this.maxPerPage)
        },
        pageOffest () {
        return this.maxPerPage * this.currentPage
        },
        paginatedOrders () {
        return this.orders.slice(0, this.currentPage * this.maxPerPage)
        }
    },
    methods: {
        loadMore () {
        this.currentPage += 1
        }
    }
}
